* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html, 
body {
    height: 100vh;
    height: fill-available;
}

@keyframes animation__bg__waves {
    @for $i from 0 through 100 {
        #{$i * 1%} {
            background: linear-gradient(
                to right,
                #cccccc -100% #{($i - 65) * 1%},
                #eeeeee #{($i - 55) * 1%} #{($i - 45) * 1%},
                #cccccc #{($i - 35) * 1%} #{($i + 35) * 1%},
                #eeeeee #{($i + 45) * 1%} #{($i + 55) * 1%},
                #cccccc #{($i + 65) * 1%} 200%
            );
        }
    }
}

button {
    padding: 5px;
    border-radius: 10px;
    border: 1px solid #333333;
    color: #333333;
    background-color: #efefef;
    cursor: pointer;
    &:hover {
        color: #efefef;
        background-color: #aaaaaa;
    }
    &:disabled {
        border: 1px solid #999999;
        color: #999999;
        background-color: #dddddd;
        cursor: default;
    }
}

.main {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100vh;
    height: fill-available;
    width: 100vw;
    background-color: #cccccc;
    animation: animation__bg__waves 5s linear 0.01s infinite normal both;;
}

.controlPanel {
    min-height: 10vh;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

.cardsCountPanel {
    display: flex;
}

.cardsCountBtn {
    flex-shrink: 0;
    border-radius: 0;
    cursor: pointer;
}

.startGameBtn {
    cursor: pointer;
}

.board {
    flex-grow: 1;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 5px;
}

.card {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 18%;
    margin: 0 0.5%;
    cursor: pointer;
}

.card_open {
    cursor: default;
}

.theEnd {
    position: absolute;
    display: none;
    top: 0;
    left: 0;
    height: 100vh;
    height: fill-available;
    width: 100vw;
    background-color: #55555555;
    cursor: pointer;
}

.theEndModal {
    position: absolute;
    display: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #dddddd;
    min-height: 10vh;
    min-width: 30vw;
    max-height: 100vh;
    max-width: 100vw;
    border: 5px solid #333333;
    border-radius: 2vh;
    padding: 2vh;
}

@media screen and (orientation: portrait) {
    .theEndModal {
        border-radius: 2vw;
        padding: 2vw;
        min-width: 80vw;
    }
}

.theEndModalCloseBtnWrapper {
    display: flex;
    justify-content: flex-end;
}

.theEndModalCloseBtn {
    padding: 5%;
    margin-top: 5%;
    font-weight: 600;
}
